/* Packages */
import React, { Component } from 'react';
import styled from '@emotion/styled';
import propTypes from 'prop-types';
import ReactTooltip from 'react-tooltip';

/* Constants */
import { COLORS, MEDIA_QUERIES, SCREEN_SIZES } from '../../constants';

/**
 * Customized from 'react-tooltip' project:
 *
 * https://github.com/wwayne/react-tooltip
 * https://react-tooltip.netlify.com/
 */

/* Component Wrapper */
const TooltipWrapper = styled('span')(props => ({
    display      : 'inline-block',
    verticalAlign: 'middle',
    lineHeight   : 0,

    '.aph-tooltip': {
        opacity: 1,
        padding: '8px 12px',

        fontSize  : '12px',
        lineHeight: '16px',
        textAlign : (props.place === 'right' ? 'left' : (props.place === 'left' ? 'right' : 'center')),

        maxWidth    : '230px',
        borderRadius: '4px',

        '&.type-error': {
            backgroundColor: COLORS.ERROR,

            '&:after': {
                borderTopColor: COLORS.ERROR,
            },

            '&.place-right:after': {
                borderRightColor:  COLORS.ERROR,
            },

            '&.place-bottom:after': {
                borderBottomColor:  COLORS.ERROR,
            },

            '&.place-left:after': {
                borderLeftColor:  COLORS.ERROR,
            },
        },

        '&.type-warning': {
            color          : COLORS.BLACK,
            backgroundColor: COLORS.WARNING,

            '&:after': {
                borderTopColor: COLORS.WARNING,
            },

            '&.place-right:after': {
                borderRightColor:  COLORS.WARNING,
            },

            '&.place-bottom:after': {
                borderBottomColor:  COLORS.WARNING,
            },

            '&.place-left:after': {
                borderLeftColor:  COLORS.WARNING,
            },
        },

        '&.type-success': {
            backgroundColor: COLORS.SUCCESS,

            '&:after': {
                borderTopColor: COLORS.SUCCESS,
            },

            '&.place-right:after': {
                borderRightColor:  COLORS.SUCCESS,
            },

            '&.place-bottom:after': {
                borderBottomColor:  COLORS.SUCCESS,
            },

            '&.place-left:after': {
                borderLeftColor:  COLORS.SUCCESS,
            },
        },

        '&.type-info': {
            backgroundColor: COLORS.INFO,

            '&:after': {
                borderTopColor: COLORS.INFO,
            },

            '&.place-right:after': {
                borderRightColor:  COLORS.INFO,
            },

            '&.place-bottom:after': {
                borderBottomColor:  COLORS.INFO,
            },

            '&.place-left:after': {
                borderLeftColor:  COLORS.INFO,
            },
        },

        '&.place-right, &.place-bottom, &.place-left': {
            '&:after': {
                borderTopColor: 'transparent',
            },
        },

        '&.type-light': {
            color          : COLORS.DARK_GREY,
            backgroundColor: COLORS.WHITE,
            boxShadow      : '0 0 4px 0 rgba(0, 0, 0, 0.2)',

            '&:after': {
                width : '12px',
                height: '12px',
                border: '0 !important',
                bottom: '-10px',

                backgroundRepeat: 'no-repeat',
                backgroundSize  : '100%',
                backgroundImage : 'url("data:image/png;base64,")',
            },

            '&.place-right:after, &.place-left:after': {
                width    : '8px',
                marginTop: '-5.5px',
            },

            '&.place-right:after': {
                backgroundImage: 'url("data:image/png;base64,")',
            },

            '&.place-bottom:after': {
                backgroundImage: 'url("data:image/png;base64,")',
            },

            '&.place-left:after': {
                backgroundImage: 'url("data:image/png;base64,")',
            },
        },

        '&__content': {
            display      : 'inline-block',
            verticalAlign: 'middle',
            minHeight    : props.style && props.style.height || null,
        },
    },

    [MEDIA_QUERIES.XS]: {
        '.aph-tooltip': {
            fontSize  : '14px',
            lineHeight: '20px',
        },
    },
}));

/* Component it self */
const Tooltip = (props) => {
    let customProps = Object.assign({}, props);
    customProps.id  = customProps.id || `AphTooltip${Math.random()}`;

    delete customProps.children;
    delete customProps.tip;

    return (
        <TooltipWrapper {...customProps}>
            <span data-tip={props.tip}
                  data-for={customProps.id}
                  className="aph-tooltip__content">
                {props.children}
            </span>
            <ReactTooltip
                {...customProps}
                delayHide={customProps.delayHide || (window.innerWidth > SCREEN_SIZES.SM ? 500 : 0)}
                className={`aph-tooltip ${props.className || ''}`}
            />
        </TooltipWrapper>
    );
};

/* Component default properties */
Tooltip.defaultProps = {
    border: false,
    effect: 'solid',
    place : 'top',
    type  : 'light',
};

/* Component properties types */
Tooltip.propTypes = {
    children : propTypes.any.isRequired,
    tip      : propTypes.any.isRequired,
    delayHide: propTypes.number,
    effect   : propTypes.string,
    place    : propTypes.string,
    type     : propTypes.string,
};

/* Exporting */
export default Tooltip;
